* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  background-color: rgb(104, 133, 133);
  min-height: 100vh;
  font-size: 23px;
  font-family: 'Poppins', sans-serif;
  color: black;
}

.container {
  background-color: rgb(213, 228, 230);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 30px;
}

.proPic {
  height: 270px;
  width: 270px;
  border-radius: 50%;
  margin: 80px 0 20px 0;
  border: 5px solid rgb(243, 247, 247);
  background-image: url(img/profPic.jpg);
  background-size: cover;
  background-position: center;
}

button {
  height: 45px;
  width: 100px;
  border-radius: 10px;
  margin: 10px;
  font-weight: bold;
  font-size: 17px;
  border-style: none;
  background-color: rgb(243, 247, 247);
  color: black;
  box-shadow: 5px 5px rgb(196, 211, 212);
}

button:hover {
  background-color: rgb(228, 72, 92);
  color: white;
}

.count {
  display: flex;
  gap: 20px;
  font-size: 35px;
}

.foot {
  display: flex;
  gap: 20px;
  font-size: 40px;
}

.fa-thumbs-up {
  color: rgb(228, 72, 92);
}

.fa-thumbs-up:hover {
  color: rgb(230, 40, 65);
}

.fa-linkedin {
  color: rgb(25, 25, 160);
}

.fa-github {
  color: black;
}

@media (min-width: 1000px) {
  body {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .container {
    min-height: 70vh;
    height: 800px;
    width: 500px;
    gap: 20px;
    border-radius: 10px;
    box-shadow: 5px 5px rgb(84, 107, 107);
  }

  .proPic {
    margin: 85px 0 10px 0;
  }
}